  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

*{
  margin: 0;
  padding: 0;

}

main {
  height: 100vh;
  /* display: grid; */
  /* grid-template-rows: 60px 100px 1fr 50px; */
}

p{
  color: black;
}
.node {
  width: 25px;
  height: 25px;
  border: 1px solid rgb(175, 216, 248);
  display: inline-block;
  margin: -1px 0 0 -1px;
}

.node-finish {
  background-color: red;
}

.node-start {
  background-color: green;
}

.node-visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: inherit;
          animation-play-state: inherit;
}

@-webkit-keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

@keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

.node-wall {
  /* animation-name: wall;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running; */
  background-color: black;
}

@-webkit-keyframes wall {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes wall {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.node-shortest-path {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}

@keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}

.GridMap_gridMap__1N6V6 {
  margin: 0;
  padding: 0;
  display: inline-block;
  outline: none;
  border-collapse: collapse;
  background-color: white;
}

.GridMap_nodeRow__30s_T{
  height: 25px;
}
.wrapper{
    display: grid;
    grid-template-columns: 402px 3fr 300px ;
    background-color: rgb(27, 27, 27);
    align-items: center;
    height:80px
}
h1,li,div,button{
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: aqua;
    text-decoration: none;
}

button:disabled{
    background-color: rgba(0,136,100,0.8);
}

h1{
    font-size: 24px;
}

.nav__links{
    list-style: none;
}

.nav__links li{
    display: inline-block;
    padding: 0 20px;
}

.nav__links li div{
    transition: all 0.3s ease 0s;
}
.nav__links li div:hover{
    transition: all 0.3s ease 0s;
    color: #0088a9;
}
.active{
    color: #0088a9;
}

button{
    padding: 9px 25px;
    background-color: rgba(0,136,169,1);
    border: none;
    border-radius: 50px;
    transition: all 0.3s ease 0s;
    cursor: pointer;

}
button:hover{
    background-color: rgba(0,136,100,0.8);
}
.startButton{
    width: 100%;
    height: 50%;
}
.clearButton{
    width: 50%;
    height: 50%;
}


