@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500&display=swap');

.wrapper{
    display: grid;
    grid-template-columns: 402px 3fr 300px ;
    background-color: rgb(27, 27, 27);
    align-items: center;
    height:80px
}
h1,li,div,button{
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: aqua;
    text-decoration: none;
}

button:disabled{
    background-color: rgba(0,136,100,0.8);
}

h1{
    font-size: 24px;
}

.nav__links{
    list-style: none;
}

.nav__links li{
    display: inline-block;
    padding: 0 20px;
}

.nav__links li div{
    transition: all 0.3s ease 0s;
}
.nav__links li div:hover{
    transition: all 0.3s ease 0s;
    color: #0088a9;
}
.active{
    color: #0088a9;
}

button{
    padding: 9px 25px;
    background-color: rgba(0,136,169,1);
    border: none;
    border-radius: 50px;
    transition: all 0.3s ease 0s;
    cursor: pointer;

}
button:hover{
    background-color: rgba(0,136,100,0.8);
}
.startButton{
    width: 100%;
    height: 50%;
}
.clearButton{
    width: 50%;
    height: 50%;
}

